<template>
    <div class="details">
        <!-- 内容区域 -->
        <div class="details-scroll">
            <div class="content-container">
                <div class="swipe-wrapper">
                    <mt-swipe :auto="4000" ref="swiper">
                        <mt-swipe-item v-for="(item,index) in detailsData.imgArr" :key="index">
                            <my-image class="my-img" :imgUrl="item"></my-image>
                        </mt-swipe-item>
                    </mt-swipe>
                </div>
                <div class="meal-tiltle">
                    <div class="title-left">
                        <p class="name">{{mealName}}</p>
                        <p class="slogan">{{slogan}}</p>
                    </div>
                    <div class="title-right">
                        <p class="price">{{singlePriceShow}}</p>
                    </div>
                </div>

                <div class="meal-content">
                   <div class="ql-container ql-snow">
                      <div class="ql-editor">
                          <div class="meal-content-container" ref="mealDetails">
                          </div>
                      </div>
                  </div>
                </div>
            </div>
        </div>
        <!-- 底部按钮 -->
        <template v-if="hasBuy">
          <mt-button disabled size="large" class="bottom-btn hasbuy-btn" type="default">{{boughtWord}}</mt-button>
        </template>
        <template v-else>
          <mt-button size="large" class="bottom-btn buy-btn" type="default" @click.native="buyHandle">{{bottomButtonWord1}}</mt-button>
        </template>
        <!-- 弹出框区域 -->
        <bind-store-popup :prop-show="showBindPopup" @updataBindStoreShow="updataBindStoreShow"></bind-store-popup>
        <mt-popup class="select-popup" position="bottom" popup-transition="popup-slide" :modal=true v-model="showPopup">
            <div class="popup-content" @touchmove.prevent>
              <!-- 选择美体师 -->
              <details-therapists ref="therapistPart" class="therapist-part" @therapistIsEmpty="therapistIsEmpty" @selectTherapist="selectTherapist" :is-show="showTherapists"></details-therapists>
              <!-- 数量价格 -->
              <div class="goods-part">
                  <!-- 数量选择 -->
                  <div class="num-box">
                      <span class="left">{{type == "2"?'兑换':'购买'}}数量</span>
                      <div class="right">
                          <span class="tip">每笔订单限{{type == "2"?'兑':'购'}}{{maxBuy}}份</span>
                          <div class="right-right">
                            <div class="input-box" @click="reduceNumHandle" :class="[{'disabled':buyNum==1}]">
                              <span class="iconfont icon-jian"></span>
                            </div>
                            <span class="num">{{buyNum}}</span>
                            <div class="input-box" @click="addNumHandle" :class="[{'disabled':buyNum>=maxBuy}]">
                                <span class="iconfont icon-jia"></span>
                            </div>
                          </div>
                      </div>
                  </div>
                  <!-- 总计结算 -->
                  <div class="acount">
                    <div v-if="type == '2'" class="left">
                      <span class="iconfont icon-gouwuche"></span><span class="emphasize">{{buyNum}}</span>份，共计<span class="emphasize">{{totalPrice}}</span>积分
                    </div>
                    <div v-else class="left">
                      <span class="iconfont icon-gouwuche"></span><span class="emphasize">{{buyNum}}</span>份，共计<span class="emphasize">{{totalPrice}}</span>元
                    </div>
                    <div class="right">
                      <mt-button size="large" class="exchange-btn" type="default" @click.native="popupBuyHandle">{{bottomButtonWord2}}</mt-button>
                    </div>
                  </div>
              </div>
            </div>
        </mt-popup>
    </div>
</template>

<script>
import bindStorePopup from "./bindStorePopup.vue";
import detailsTherapists from "./detailsTherapists.vue";
import $ from "jquery";
import { Toast, Popup, Button, Swipe, SwipeItem } from "mint-ui";
import Vue from "vue";
import "mint-ui/lib/swipe/style.css";
import "mint-ui/lib/toast/style.css";
import "mint-ui/lib/swipe-item/style.css";
import "mint-ui/lib/button/style.css";
import "mint-ui/lib/popup/style.css";
import { mapState } from "vuex";
import sharejs from "../../common/js/sharejs.js";
import myImage from "../lazyloadImg/lazyImage.vue";
import { savaToLocal, loadFromlLocal } from "../../common/js/storage.js"; // 公共方法：本地缓存

Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
Vue.component(Button.name, Button);
Vue.component(Popup.name, Popup);
Vue.component(Toast.name, Toast);

export default {
  name: "v-details",
  data() {
    return {
      detailsData: {},
      mealCover: "",
      price: 0,
      cardId: "",
      slogan: "",
      mealName: "",
      hasBuy: false,
      iconurl: "",
      showPopup: false,
      buyNum: 1,
      type: "0",
      buyType: "1", //1代表一次只能购买一个、2代表一次可以购买多个
      therapistsEmpty: false, //和用户绑定的店铺美容师列表是否为空，起始值假设不为空
      orderTherapist: null, //购买商品选择的美体师
      showBindPopup:false   //是否显示绑定店铺弹出框
    };
  },
  components: { myImage, detailsTherapists,bindStorePopup },
  computed: {
    ...mapState([
      "path",
      "role",
      "phoneno",
      "userid",
      "curMealData",
      "source",
      "token",
      "bindStoreInfo"
    ]),
    maxBuy() {
      if (this.buyType == "1") return 1;
      else return 30;
    },
    totalPrice() {
      return this.buyNum * this.price;
    },
    totalPriceWord() {
      if (this.type == "2") return this.totalPrice + "积分";
      else return this.totalPrice + "元";
    },
    singlePriceShow() {
      if (this.type == "2") {
        //积分商品
        return this.price + "积分";
      } else {
        //非积分商品
        return "¥ " + this.price;
      }
    },
    bottomButtonWord1() {
      if (this.type == "2") return "立即兑换";
      else return "立即购买";
    },
    bottomButtonWord2() {
      if (this.type == "2") return "兑换";
      else return "购买";
    },
    boughtWord() {
      if (this.type == "2") return "已兑换";
      else return "已购买";
    },
    popupTitle() {
      if (this.type == "2") {
        //积分商品
        return "请选择需要兑换的数量";
      } else {
        //非积分商品
        return "请选择需要购买的数量";
      }
    },
    showTherapists() {
      //是否要显选择美体师列表
      if (!this.bindStoreInfo) return false;
      if (this.type == "2") return false;
      if (this.therapistsEmpty) return false;
      if (parseInt(this.role) > 1 && parseInt(this.role) < 6) {
        return false;
      }
      return true;
    }
  },
  beforeRouteLeave(to, from, next) {
    sharejs.defultShareMsg();
    sharejs.getWxData();
    if (this.$refs.therapistPart) {
      this.$refs.therapistPart.destroyHandle();
    }
    next();
  },
  created() {
    this.cardId = this.curMealData.cardId;
    this.getDetailsData();
  },
  methods: {
    //更新绑定店铺弹出框显示状态
    updataBindStoreShow(boo){
      this.showBindPopup=boo;
    },
    //当用户绑定的商家美体师列表为空时收到的事件
    therapistIsEmpty() {
      this.therapistsEmpty = true;
    },
    //购买商品选择美体师，如果为空就说没有选择
    selectTherapist(data) {
      this.orderTherapist = data;
    },
    //减少购买数量
    reduceNumHandle() {
      if (this.buyNum <= 1) return;
      this.buyNum--;
    },
    //增加购买数量
    addNumHandle() {
      if (this.buyNum >= this.maxBuy) return;
      this.buyNum++;
    },
    buyHandle(event) {
      if (!this.token) {
        this.$router.push({ path: "/login" });
        return;
      }
      if (parseInt(this.role) > 2 && parseInt(this.role) < 6) {
        //角色不是客户也不是商家
        Toast({
          message: "您的身份不能购买或兑换此产品！",
          iconClass: "iconfont icon-tishi1",
          duration: 2000
        });
      } else {
        if (this.role == "2" && this.type == "2") {
          //商家购买积分商品
          Toast({
            message: "商家无法兑换积分商品!",
            iconClass: "iconfont icon-tishi1",
            duration: 2000
          });
          return;
        } else if (this.role != "2") {
          //客户或者是美体师购买商家小店铺商品
          if (this.type == "3") {
            Toast({
              message: "只有商家才能购买!",
              iconClass: "iconfont icon-tishi1",
              duration: 2000
            });
            return;
          } else {
            //客户或者是美体师没有绑定店铺
            if (!this.bindStoreInfo) {
              this.showBindPopup=true;
              return;
            }
          }
        }
        this.showNumPopup();
      }
    },
    popupBuyHandle(event) {
      if (event._constructed) return;
      if (this.type == "2") {
        if (this.totalPrice > this.source) {
          Toast({
            message: "您的积分不足",
            duration: 1500,
            position: "bottom"
          });
          let toast = document.getElementsByClassName("mint-toast")[0];
          toast.style.zIndex = 7000;
          // toast.style.color="yellow";
        } else this.requestBuyExchangeGoods();
      } else this.jumpToPayPage();
    },
    jumpToPayPage() {
      let payInfoObj = {
        goodId: this.cardId,
        goodDec: this.slogan,
        price: this.price,
        goodName: this.mealName,
        goodIcon: this.path + this.iconurl,
        num: this.buyNum
      };
      //如果选择了美体师，将美体师的id带入支付界面
      if (this.orderTherapist) {
        payInfoObj.beautiesid = this.orderTherapist.beautiesid;
      }
      savaToLocal("aml", "payInfo", payInfoObj);
      this.$router.push({ path: "/payAffirm" });
    },
    requestBuyExchangeGoods() {
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + "php/Route.php", {
          type: "busourcegoods",
          gid: this.cardId,
          uid: this.userid,
          num: this.buyNum
        })
        .then(response => {
          let resultData = response.body;
          this.showPopup = false;
          if (resultData.error == "ok") {
            this.$store.commit("UPDATE_SOURCE", this.source - this.totalPrice);
            Toast({
              message: "兑换成功",
              iconClass: "iconfont icon-tishi1",
              duration: 1500
            });
          } else if (resultData.error == "-102") {
            Toast({
              message: "只能兑换一次",
              iconClass: "iconfont icon-tishi1",
              duration: 1500
            });
          } else {
            Toast({
              message: "兑换失败!",
              iconClass: "iconfont icon-tishi1",
              duration: 1500
            });
          }
          this.$store.dispatch("toggleLoading", false);
        })
        .catch(response => {
          //请求出错处理
          console.log(response);
          this.$store.dispatch("toggleLoading", false);
        });
    },
    showNumPopup() {
      this.showPopup = true;
      //如果可以选择美体师
      if (this.$refs.therapistPart) {
        this.$refs.therapistPart.resetHandle();
        this.orderTherapist = null;
      }
    },
    getDetailsData() {
      this.$http
        .post(this.path + `php/ttl/goodsFunction.php`, {
          type: "goodsmgs",
          userid: this.userid,
          goodsid: this.cardId
        })
        .then(response => {
          let data = response.body;
          let detailsData = { imgArr: [], insetHtml: "" };
          data.imgarr.forEach((val, index, arr) => {
            detailsData.imgArr.push(this.path + val.iconurl);
          });
          if (data.htmlmsg) detailsData.insetHtml = data.htmlmsg;
          this.detailsData = detailsData;
          this.price = data.price;
          this.slogan = data.message;
          this.mealName = data.gname;
          this.iconurl = data.icon;
          this.hasBuy = !Boolean(data.iscanbuy);
          this.buyType = data.buyType.toString();
          this.type = data.type.toString();
          if (this.type.toString() == "2") this.price = parseInt(this.price);
          var mealDetails = this.$refs.mealDetails;
          let showHtml = this.detailsData.insetHtml;
          $(mealDetails).html(showHtml);
          this.updataShare();
        })
        .catch(response => {
          //请求出错处理
          console.log(response);
        });
    },
    updataShare() {
      let shareUrl =
        this.$store.state.domainUrl +
        "wxapi/GetUserInfo/dropindex.php?type=amlzlogin&detailsid=" +
        this.cardId +
        "&userid=" +
        this.userid;
      let shareimg = this.path + this.iconurl;
      sharejs.updateShareMsg(shareUrl, shareimg, this.slogan, this.mealName);
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import '../../common/css/mixin.styl';
@import './quill.bubble.css';
@import './quill.core.css';
@import './quill.snow.css';

.details {
  font-family: 'Microsoft YaHei';
  width: 100%;

  .details-scroll {
    // background-color: #fff;
    width: 100%;
    padding-bottom: 0.5rem;

    .content-container {
      .swipe-wrapper {
        width: 100%;

        .mint-swipe {
          width: 3.2rem;
          height: 1.6rem;

          .my-img {
            background-color: #f1f1f1;
            width: 100%;
            height: 100%;
          }

          .mint-swipe-indicators {
            bottom: 0.05rem;

            .mint-swipe-indicator {
              background-color: #fff;
              margin: 0 0.03rem;
            }

            .mint-swipe-indicator.is-active {
              background-color: #f8ef94;
              opacity: 0.8;
            }
          }
        }
      }

      .meal-tiltle {
        height: 0.6rem;
        padding: 0 0.18rem;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .title-left {
          .name {
            color: #222;
            font-size: 0.14rem;
            font-weight: 600;
            // letter-spacing: 0.01rem;
          }

          .slogan {
            margin-top: 0.05rem;
            font-size: 0.13rem;
            line-height: 0.14rem;
            color: #898989;
            letter-spacing: 0.005rem;
          }
        }

        .title-right {
          flex-shrink: 0;

          .price {
            color: #FD8709;
            font-weight: 500;
            font-size: 0.15rem;
          }
        }
      }

      .meal-content {
        width: 100%;
        border-top-1px(#EEEEEE);
      }
    }
  }

  .bottom-btn {
    position: fixed;
    width: 87%;
    bottom: 0.12rem;
    left: 6.5%;
    font-size: 0.13rem;
    letter-spacing: 0.02rem;
    height: 0.3rem;
    box-shadow: none;

    &.hasbuy-btn {
      color: #8a8a8a;
      background-color: #ddd;
      opacity: 0.9;
    }

    &.buy-btn {
      background-color: #f8ef94;
      color: #a2733d;
      opacity: 0.9;
    }
  }

  .select-popup {
    width: 100%;
    border-radius: 0.04rem 0.04rem 0px 0px;
    padding-top: 0.1rem;

    .popup-content {
      padding: 0 0.1rem;
      box-sizing: border-box;

      .therapist-part {
      }

      .goods-part {
        width: 100%;

        .num-box {
          height: 0.5rem;
          display: flex;
          align-items: center;
          justify-content: space-between;

          .left {
            font-size: 0.13rem;
            color: #666;
          }

          .right {
            display: flex;
            align-items: center;

            .tip {
              color: #FD8709;
              font-size: 0.12rem;
              margin-right: 0.1rem;
            }

            .right-right {
              display: flex;
              align-items: center;

              .input-box {
                width: 0.22rem;
                height: 0.22rem;
                display: flex;
                align-items: center;
                justify-content: center;
                border: 1px #e3e3e3 solid;
                border-radius: 0.02rem;

                .iconfont {
                  color: #000;
                  font-size: 0.13rem;
                }

                &.disabled {
                  .iconfont {
                    color: #e3e3e3;
                  }
                }
              }

              .num {
                color: #000;
                font-size: 0.13rem;
                margin: 0 0.08rem;
              }
            }
          }
        }

        .acount {
          height: 0.5rem;
          display: flex;
          align-items: center;
          justify-content: space-between;
          border-top: 1px #ddd dashed;

          .left {
            font-size: 0.12rem;
            color: #999;

            .icon-gouwuche {
              font-size: 0.15rem;
              color: #a2733d;
              margin-right: 0.01rem;
            }

            .emphasize {
              font-size: 0.13rem;
              color: #a2733d;
              margin: 0 0.02rem;
            }
          }

          .right {
            .exchange-btn {
              font-size: 0.13rem;
              background-color: #f8ef94;
              color: #a2733d;
              height: 0.26rem;
              width: 0.74rem;
              box-shadow: none;
            }
          }
        }
      }
    }
  }
}
</style>


