<template>
    <mt-popup class="bind-store-popup" popup-transition="popup-fade" :modal=true v-model="isShow">
        <div class="popup-wapper" @touchmove.prevent>
            <div class="popup-close">
                <i class="iconfont icon-close" @click="hidePopup"></i>
            </div>
            <div class="tip-icon">
                <i class="iconfont icon-gantan"></i>
            </div>
            <div class="tips">
                <p class="receive-tip">您还未绑定店铺</p>
            </div>
            <mt-button size="small" class="sure" type="default" @click="bindHandle">去绑定</mt-button>
        </div>
    </mt-popup>
</template>

<script>
import { mapState } from "vuex";
import Vue from "vue";
import { Popup, Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
import "mint-ui/lib/popup/style.css";
Vue.component(Button.name, Button);
Vue.component(Popup.name, Popup);

export default {
  name: "bind-store-popup",
  props: {
    propShow: {
      type: Boolean,
      default: false
    }
  },
  components: {},
  created() {},
  methods: {
    hidePopup(event) {
      this.isShow = false;
    },
    bindHandle() {
      this.$router.push({ path: "/bindStore" });
    }
  },
  computed: {
    ...mapState(["path"]),
    isShow: {
      get: function() {
        return this.propShow;
      },
      set: function(boo) {
        this.$emit("updataBindStoreShow", boo);
      }
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.bind-store-popup {
    width: 2.6rem;
    border-radius: 0.08rem;
    text-align: center;

    .popup-close {
        text-align: right;

        .iconfont {
            color: #999;
            font-size: 0.24rem;
            margin: 0.08rem 0.08rem 0 0;
            display: inline-block;
        }
    }

    .tip-icon {
        .iconfont {
            color: #a2733d;
            font-size: 0.44rem;
            display: inline-block;
        }
    }

    .tips {
        margin: 0.04rem 0 0.2rem 0;

        p {
            color: #999;
            font-size: 0.14rem;
        }
    }

    .sure {
        width: 1.2rem;
        height: .28rem;
        box-shadow: none;
        margin-bottom: .22rem;
        background-color: #f8ef94;
        color: #a2733d;
        // letter-spacing: 0.01rem;
        font-size: .13rem;
    }
}
</style>



