<template>
    <div class="details-therapists" v-if="isShow">
        <p class="tip">请选择为您服务的美体师（可不选）</p>
        <div ref="betterscroll" class="better-scroll">
            <div class="therapist-list">
                <div v-for="(item,index) in therapistList" class="therapist-cell" @click="selectHandle(index)" :key="item.beautiesid" :class="[{'last':index==therapistList.length-1}]">
                    <div class="left">
                        <my-image class="my-img" :imgUrl="item.headimgurl"></my-image>
                        <div class="name">{{item.username}}</div>
                    </div>
                    <div class="right">
                        <div v-show="index==curIndex" class="circle"></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import myImage from "../lazyloadImg/lazyImage.vue";
import { mapState } from "vuex";
import BScroll from "better-scroll";
import Vue from "vue";
import { Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
Vue.component(Button.name, Button);

export default {
  name: "details-therapists",
  components: {
    myImage
  },
  props: {
    isShow: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      therapistList: null,
      curPage: 0,
      curIndex: -1 //-1代表未选择
    };
  },
  mounted() {
    if(!this.isShow) return;
    this.$http
      .post(this.path + "php/Route.php?c=Store", {
        type: "showBeautiesList",
        bid: this.bindStoreInfo.bossid,
        page: this.curPage
      })
      .then(response => {
        let resultData = response.body.arr;
        if (!resultData || resultData.length == 0) {
          this.$emit("therapistIsEmpty");
        } else {
          this.therapistList = resultData;
          this.initBScroll();
        }
      })
      .catch(response => {
        //请求出错处理
        console.log(response);
        this.$store.dispatch("toggleLoading", false);
      });
  },
  computed: {
    ...mapState(["path", "userid", "bindStoreInfo"])
  },
  methods: {
    initBScroll() {
      if (this.betterscroll) return;
      this.$nextTick(() => {
        this.betterscroll = new BScroll(this.$refs.betterscroll, {
          click: true
        });
      });
    },
    commitHandle: function() {
      console.log("点击了确定并扫码按钮");
      let therapistData = null;
      if (this.curIndex != -1) {
        therapistData = this.therapistList[this.curIndex];
      }
    },
    selectHandle(chooseIndex) {
      if (this.curIndex == chooseIndex) {
        this.curIndex = -1;
        this.$emit("selectTherapist", null);
      } else {
        this.curIndex = chooseIndex;
        let curData = this.therapistList[chooseIndex];
        this.$emit("selectTherapist", curData);
      }
    },
    resetHandle() {
      this.curIndex = -1;
      this.$nextTick(() => {
        if (this.betterscroll) this.betterscroll.refresh();
      });
    },
    show() {
      this.isShow = true;
    },
    hide() {
      this.isShow = false;
    },
    addHandle(event) {
      if (event._constructed) return; // 去掉betterscroller自带click事件的点击
      this.hide();
      this.$router.push({ name: "therapistList" });
    },
    destroyHandle() {
      if (this.betterscroll) {
        this.betterscroll.destroy();
      }
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.details-therapists {
  width: 100%;
  margin-bottom: 0.04rem;

  .tip {
    font-size: 0.13rem;
    color: #666;
    margin: 0.18rem 0 0.09rem 0.02rem;
  }

  .had-choose {
    text-align: right;
    color: #666;
    font-size: 0.13rem;
    height: 0.28rem;
    line-height: 0.28rem;
    padding-top: 0.02rem;

    .name {
      color: #a2733d;
      font-size: 0.13rem;
    }
  }

  .better-scroll {
    overflow: hidden;
    border: 1px #ddd dashed;
    border-radius: 0.02rem;
    height: 30vh;

    .therapist-list {
      box-sizing: border-box;
      padding: 0 0.06rem;

      .therapist-cell {
        width: 100%;
        height: 0.52rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: #f1f1f1;
        border-radius: 0.04rem;
        border-top: 0.04rem #fff solid;

        &.last {
          border-bottom: 0.04rem #fff solid;
        }

        .left {
          display: flex;
          align-items: center;

          .my-img {
            width: 0.4rem;
            height: 0.4rem;
            margin-left: 0.04rem;
            margin-right: 0.06rem;
          }

          .name {
            font-size: 0.12rem;
            color: #666;
          }
        }

        .right {
          margin-right: 0.1rem;
          width: 0.16rem;
          height: 0.16rem;
          border-radius: 50%;
          border: 1px #a2733d solid;
          display: flex;
          align-items: center;
          justify-content: center;

          .circle {
            width: 0.08rem;
            height: 0.08rem;
            border-radius: 50%;
            background-color: #a2733d;
          }
        }
      }
    }
  }
}
</style>





